import { ArrowLeft, MessageSquare, Clock, Video as VideoIcon, Download, Send, Eye, Calendar, Users, FileText, Mic, Zap, AlertCircle, CheckCircle, ChevronDown, ChevronUp, Info, Trash2, Edit3, Save, X, ClipboardCheck, Target, Maximize2, Columns, GraduationCap, Play, Square, RefreshCw, SkipForward, Crown, Sparkles, Brain, TrendingUp, Award, Quote } from 'lucide-react';

// Import types and utilities
import type {
  Video,
  Comment,
  CustomTemplate,
  VideoPlayerState,
  ActiveLabel,
  EvaluationData,
  SpeechRecognition,
  SpeechRecognitionEvent,
  SpeechRecognitionErrorEvent
} from './types/session.types';
