  const handleCommentChange = (videoTitle: string, comment: string) => {
    setVideoPlayerStates(prevStates => {
      const newStates = new Map(prevStates);
      const currentState = newStates.get(videoTitle) || {
        isPlaying: false,
        currentTime: 0,
        newComment: ''
      };

      newStates.set(videoTitle, {
        ...currentState,
        newComment: comment
      });

      return newStates;
    });
  };

