  // New label system functions
  const handleStartLabel = (videoTitle: string, comment: string, type: string = 'positive') => {
    const targetVideo = getTargetVideoForComments();
    if (!targetVideo) return;

    const videoState = videoPlayerStates.get(videoTitle);
    if (!videoState) return;

    const newLabel: ActiveLabel = {
      id: `${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
      videoTitle,
      startTime: videoState.currentTime,
      comment,
      type,
      createdAt: Date.now()
    };

    setActiveLabels(prev => [...prev, newLabel]);
    toast.success(`Label started at ${formatTime(videoState.currentTime)}`);
    setLabelStatusMessage(`Label recording started at ${formatTime(videoState.currentTime)}`);

    // Clear the comment input
    handleCommentChange(videoTitle, '');
  };

  const handleEndLabel = async (labelId: string) => {
    const targetVideo = getTargetVideoForComments();
    if (!targetVideo) return;

    const videoState = videoPlayerStates.get(targetVideo.title);
    if (!videoState) return;

    const activeLabel = activeLabels.find(label => label.id === labelId);
    if (!activeLabel) return;

    const endTime = videoState.currentTime;
    const duration = Math.round(endTime - activeLabel.startTime);

    if (duration <= 0) {
      toast.error('End time must be after start time');
      return;
    }

    // Clear AI suggestion when stopping a label
    clearSuggestion();

    // Capture the current comment from the input field
    const currentComment = videoPlayerStates.get(targetVideo.title)?.newComment || activeLabel.comment;

    // Validate that comment is not empty
    if (!currentComment || currentComment.trim() === '') {
      toast.error('Please enter a comment before stopping the label');
      return;
    }

    try {
      const sessionReference = getSessionTimelineReference();
      if (!sessionReference) {
        toast.error('No session video reference available');
        return;
      }

      // Create the comment with calculated duration
      const commentData = {
        session: sessionName,
        video_title: sessionReference.title,
        timestamp: activeLabel.startTime,
        comment_text: currentComment,
        duration: duration,
        comment_type: activeLabel.type,
        comment_color: selectedCommentColor // Add the selected color
      };

      const response = await addComment(commentData);

      if (response && response.message) {
        const responseData = response.message;

        if (responseData.message === 'Success') {
          toast.success(`Label completed: ${formatTime(duration)} duration`);
          setLabelStatusMessage(`Label completed with ${formatTime(duration)} duration`);

          // Store the comment color in local map if we have a comment name, otherwise store as pending
          const commentName = responseData.data?.name;
          if (commentName) {
            console.log(` Storing color for label comment ${commentName}: ${selectedCommentColor}`);
            setCommentColorMap(prev => new Map(prev.set(commentName, selectedCommentColor)));
          } else {
            // Store as pending comment with text+video as key
            const pendingKey = `${currentComment}_${activeLabel.videoTitle}`;
            console.log(` Storing pending color for label ${pendingKey}: ${selectedCommentColor}`);
            setPendingCommentColors(prev => new Map(prev.set(pendingKey, {
              color: selectedCommentColor,
              timestamp: Date.now()
            })));
          }

          // Remove the active label
          setActiveLabels(prev => prev.filter(label => label.id !== labelId));

          // Clear the comment input
          handleCommentChange(targetVideo.title, '');

          // Refresh comments
          await refreshSession();
        } else if (responseData.error) {
          toast.error(`Error: ${responseData.error}`);
        }
      }
    } catch (error) {
      console.error('Error completing label:', error);

      // Handle CSRF token errors specifically with retry
      if (error && typeof error === 'object' && error !== null) {
        const errorObj = error as any;
        if (errorObj.exc_type === 'CSRFTokenError' || errorObj.exception?.includes('CSRFTokenError')) {
          toast.loading('Session expired, attempting to refresh...', { id: 'csrf-retry' });

          try {
            // Retry the operation after refreshing session
            await handleCSRFError(async () => {
              const retrySessionReference = getSessionTimelineReference();
              if (!retrySessionReference) {
                throw new Error('No session video reference available');
              }

              const commentData = {
                session: sessionName,
                video_title: retrySessionReference.title,
                timestamp: activeLabel.startTime,
                comment_text: currentComment,
                duration: duration,
                comment_type: activeLabel.type,
                comment_color: selectedCommentColor
              };

              const retryResponse = await addComment(commentData);

              if (retryResponse && retryResponse.message && retryResponse.message.message === 'Success') {
                toast.success(`Label completed: ${formatTime(duration)} duration`, { id: 'csrf-retry' });
                setLabelStatusMessage(`Label completed with ${formatTime(duration)} duration`);

                // Store the comment color and clean up
                const responseData = retryResponse.message;
                const commentName = responseData.data?.name;
                if (commentName) {
                  setCommentColorMap(prev => new Map(prev.set(commentName, selectedCommentColor)));
                }

                setActiveLabels(prev => prev.filter(label => label.id !== labelId));

                // Clear the comment input
                handleCommentChange(targetVideo.title, '');

                await refreshSession();
              } else {
                throw new Error('Retry failed');
              }
            });
          } catch (retryError) {
            toast.error('Session refresh failed. Please refresh the page manually.', {
              id: 'csrf-retry',
              duration: 5000
            });
          }
          return;
        }
        if (errorObj.httpStatus === 403 || errorObj.httpStatus === 401) {
          toast.error('Authentication failed. Please refresh the page and log in again.', {
            duration: 5000,
            id: 'auth-error'
          });
          return;
        }
      }

      toast.error('Failed to complete label. Please try again.');
    }
  };

