  const handleAddComment = async (videoTitle: string, customTimestamp?: number, customDuration?: number, customCommentType?: string) => {
    const videoState = videoPlayerStates.get(videoTitle);
    if (!videoState || !videoState.newComment.trim()) return;

    try {
      // Store current time before adding comment
      const currentTime = customTimestamp !== undefined ? customTimestamp : videoState.currentTime;

      // Pause the video
      handlePlayPause(videoTitle, false);

      // Get student name if selected
      let studentName = null;
      if (selectedStudentForComment === 'student1') {
        studentName = sessionData?.session.student_1_name || 'Student 1';
      } else if (selectedStudentForComment === 'student2') {
        studentName = sessionData?.session.student_2_name || 'Student 2';
      }

      const sessionReference = getSessionTimelineReference();
      if (!sessionReference) {
        toast.error('No session video reference available');
        return;
      }

      // Prepare comment data with duration and type if provided
      const commentData: any = {
        session: sessionName,
        video_title: sessionReference.title,
        timestamp: currentTime,
        comment_text: videoState.newComment,
        comment_color: selectedCommentColor, // Add the selected color
        student_name: studentName // Add student name if selected
      };

      // Debug: Log what we're sending to backend
      console.log(' Sending comment data to backend:', {
        commentData,
        selectedStudentForComment,
        studentName,
        sessionData_students: {
          student_1_name: sessionData?.session.student_1_name,
          student_2_name: sessionData?.session.student_2_name
        }
      });

      // Add duration if provided
      if (customDuration !== undefined) {
        commentData.duration = customDuration;
      }

      // Add comment type if provided (not auto-detect)
      if (customCommentType && customCommentType !== 'auto') {
        commentData.comment_type = customCommentType;
      }

      const response = await addComment(commentData);

      if (response && response.message) {
        const responseData = response.message;
        console.log(' Comment creation response:', responseData);

        if (responseData.message === 'Success') {
          toast.success('Comment added successfully');

          // Store the comment color in local map if we have a comment name, otherwise store as pending
          const commentName = responseData.data?.name;
          if (commentName) {
            console.log(` Storing color for comment ${commentName}: ${selectedCommentColor}`);
            setCommentColorMap(prev => new Map(prev.set(commentName, selectedCommentColor)));
          } else {
            // Store as pending comment with text+video as key
            const pendingKey = `${videoState.newComment}_${videoTitle}`;
            console.log(` Storing pending color for ${pendingKey}: ${selectedCommentColor}`);
            setPendingCommentColors(prev => new Map(prev.set(pendingKey, {
              color: selectedCommentColor,
              timestamp: Date.now()
            })));
          }

          // Clear comment input
          handleCommentChange(videoTitle, '');
          // Refresh comments
          await refreshSession();

          // Restore video position after comment is added
          setVideoPlayerStates(prevStates => {
            const newStates = new Map(prevStates);
            const currentState = newStates.get(videoTitle) || {
              isPlaying: false,
              currentTime: 0,
              newComment: ''
            };

            newStates.set(videoTitle, {
              ...currentState,
              currentTime: currentTime
            });

            return newStates;
          });

          // Update video position
          const videoRef = videoRefs.current.get(videoTitle);
          if (videoRef) {
            videoRef.currentTime = currentTime;
          }
        } else if (responseData.error) {
          toast.error(`Error: ${responseData.error}`);
          console.error('API Error:', responseData.error);
        }
      } else {
        toast.error('Failed to add comment: Invalid response');
        console.error('Invalid API response:', response);
      }
    } catch (error) {
      console.error('Error adding comment:', error);

      // Handle CSRF token errors specifically with retry
      if (error && typeof error === 'object' && error !== null) {
        const errorObj = error as any;
        if (errorObj.exc_type === 'CSRFTokenError' || errorObj.exception?.includes('CSRFTokenError')) {
          toast.loading('Session expired, attempting to refresh...', { id: 'csrf-retry-comment' });

          try {
            // Retry the operation after refreshing session
            await handleCSRFError(async () => {
              const retrySessionReference = getSessionTimelineReference();
              if (!retrySessionReference) {
                throw new Error('No session video reference available');
              }

              // Get student name if selected
              let studentName = null;
              if (selectedStudentForComment === 'student1') {
                studentName = sessionData?.session.student_1_name || 'Student 1';
              } else if (selectedStudentForComment === 'student2') {
                studentName = sessionData?.session.student_2_name || 'Student 2';
              }

              const commentData: any = {
                session: sessionName,
                video_title: retrySessionReference.title,
                timestamp: customTimestamp !== undefined ? customTimestamp : videoState.currentTime,
                comment_text: videoState.newComment,
                comment_color: selectedCommentColor,
                student_name: studentName
              };

              if (customDuration !== undefined) {
                commentData.duration = customDuration;
              }

              if (customCommentType && customCommentType !== 'auto') {
                commentData.comment_type = customCommentType;
              }

              const retryResponse = await addComment(commentData);

              if (retryResponse && retryResponse.message && retryResponse.message.message === 'Success') {
                toast.success('Comment added successfully', { id: 'csrf-retry-comment' });

                // Store the comment color and clean up
                const responseData = retryResponse.message;
                const commentName = responseData.data?.name;
                if (commentName) {
                  setCommentColorMap(prev => new Map(prev.set(commentName, selectedCommentColor)));
                }

                handleCommentChange(videoTitle, '');
                await refreshSession();
              } else {
                throw new Error('Retry failed');
              }
            });
          } catch (retryError) {
            toast.error('Session refresh failed. Please refresh the page manually.', {
              id: 'csrf-retry-comment',
              duration: 5000
            });
          }
          return;
        }
        if (errorObj.httpStatus === 403 || errorObj.httpStatus === 401) {
          toast.error('Authentication failed. Please refresh the page and log in again.', {
            duration: 5000,
            id: 'auth-error'
          });
          return;
        }
      }

      toast.error('Failed to add comment. Please try again.');
    }
  };

