const SESSION_ONBOARDING_KEY = 'surgical_training_session_onboarding';
const SESSION_ONBOARDING_PERMANENT_KEY = 'surgical_training_session_onboarding_dont_show_again';

export const useSessionOnboarding = () => {
  const [isOnboardingActive, setIsOnboardingActive] = useState(false);
  const [currentStepIndex, setCurrentStepIndex] = useState(0);
  const [hasCompletedOnboarding, setHasCompletedOnboarding] = useState(false);

  useEffect(() => {
    // Check if user has permanently disabled onboarding
    const dontShowAgain = localStorage.getItem(SESSION_ONBOARDING_PERMANENT_KEY);

    if (dontShowAgain === 'true') {
      setHasCompletedOnboarding(true);
      setIsOnboardingActive(false);
      return;
    }

    // Development mode: Show onboarding on every page reload (but still respect "don't show again")
    const isDevelopment = import.meta.env.DEV;

    if (isDevelopment) {
      // In development, show onboarding unless user clicked "don't show again"
      setIsOnboardingActive(true);
      setHasCompletedOnboarding(false);
    } else {
      // Production mode: Check if user has already seen onboarding in this session
      const completedThisSession = sessionStorage.getItem(SESSION_ONBOARDING_KEY);

      if (completedThisSession === 'true') {
        setHasCompletedOnboarding(true);
      } else {
        // Show onboarding on every login/session
        setIsOnboardingActive(true);
        setHasCompletedOnboarding(false);
      }
    }
  }, []);

  const nextStep = () => {
    if (currentStepIndex < SESSION_ONBOARDING_STEPS.length - 1) {
      setCurrentStepIndex(prev => prev + 1);
    } else {
      completeOnboarding();
    }
  };

  const previousStep = () => {
    if (currentStepIndex > 0) {
      setCurrentStepIndex(prev => prev - 1);
    }
  };

  const skipOnboarding = () => {
    completeOnboarding();
  };

  const completeOnboarding = () => {
    setIsOnboardingActive(false);
    setHasCompletedOnboarding(true);
    sessionStorage.setItem(SESSION_ONBOARDING_KEY, 'true');
  };

  const dontShowAgain = () => {
    localStorage.setItem(SESSION_ONBOARDING_PERMANENT_KEY, 'true');
    setIsOnboardingActive(false);
    setHasCompletedOnboarding(true);
  };

  const resetOnboarding = () => {
    sessionStorage.removeItem(SESSION_ONBOARDING_KEY);
    localStorage.removeItem(SESSION_ONBOARDING_PERMANENT_KEY);
    setCurrentStepIndex(0);
    setIsOnboardingActive(true);
    setHasCompletedOnboarding(false);
  };

  const currentStep = SESSION_ONBOARDING_STEPS[currentStepIndex];
  const isLastStep = currentStepIndex === SESSION_ONBOARDING_STEPS.length - 1;

  return {
    isOnboardingActive,
    currentStep,
    currentStepIndex,
    totalSteps: SESSION_ONBOARDING_STEPS.length,
    isLastStep,
    hasCompletedOnboarding,
    nextStep,
    previousStep,
    skipOnboarding,
    completeOnboarding,
    dontShowAgain,
    resetOnboarding
  };
};